import {createStore,applyMiddleware,compose} from 'redux';
import todo from './reducer';
import thunk from 'redux-thunk';
//redux react-redux redux-thunk
// redux:状态管理的容器,action、Reducer、store

// let store = createStore(todo,compose(applyMiddleware(thunk),window.__REDUX_DEVTOOLS_EXTENSION__ && window.__REDUX_DEVTOOLS_EXTENSION__()));//store是一个大的仓库，从里面可以拿到想要的状态
let store = createStore(todo,applyMiddleware(thunk));
export default store;