import {combineReducers} from 'redux';

import {DELETE_ITEM,ADD_TODO_ITEM,CHANGE_INPUT_VALUE, LOGIN_SUCCESS} from '../actions/actionTypes';
let todos = [1,2,3];//声明状态

// let obj = {
//     a:[1,2,3],
//     b:2
// }
// return Object.assign({},{...obj,a:action.value});

function todo(state=todos,action){//action是惟一的数据来源
    switch (action.type) {
        case ADD_TODO_ITEM:
            console.log(action);
            return [...state,action.value];
        case DELETE_ITEM:
            return state.filter((item,idx)=>{
                return action.index !== idx;//return是true则不删除，是false则删除
            })
            // let newState = [...state];
            // newState.splice(action.index,1);
            // return newState;
        default:
            return state;//返回初始值
    }
}


let inputValue = 'todolist';
function changeValue(state=inputValue,action){
    switch (action.type) {
        case CHANGE_INPUT_VALUE:
            return action.value;
        default:
            return state;
    }
}

let userInfor = {
    loginname:'',
    score:0
}

function login(state=userInfor,action){
    switch (action.type) {
        case LOGIN_SUCCESS:
            console.log(action);
            
            return action.value;
        default:
            return state;
    }
}

let reducer = combineReducers({
    todo:todo,
    changeValue:changeValue,login
})
export default reducer;