function Todoing({dispatch,todos}){
    let delItem = (index)=>{
        dispatch({
            type: DELETE_ITEM,
            index
        })
    }
    return (
        <ul>
            {
                todos.map((item,idx)=>(
                    <li key={idx}>
                        {item}----
                        <button onClick={()=>{delItem(idx)}}>删除</button>
                    </li>
                ))
            }
        </ul>
    )
}
let mapStateToProps = (state)=> {//遍历状态返回属性中去
    return {
        todos:state.todo
    }
}

export default connect(mapStateToProps)(Todoing);//前面返回值还是一个组件 一个高阶函数 有connect之后就自动监听了
